
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as Font from 'expo-font';

import Login from './Views/Login';

import SignUp from './Views/SignUp';
// import ForgotPassword1 from './Views/ForgotPassword1'


const customFonts = {
  'Arslan': require('./assets/fonts/Arslan.ttf'),
};

const Stack = createNativeStackNavigator();


const config = {
  animation: 'spring',
  config: {
    stiffness: 1000,
    damping: 500,
    mass: 3,
    overshootClamping: true,
    restDisplacementThreshold: 0.01,
    restSpeedThreshold: 0.01,
  },
};

export default function App (){
  // DOMObject.style.backgroundColor
  Font.loadAsync(customFonts);

  return (
    <NavigationContainer>

        <Stack.Navigator screenOptions={{headerShown: false, contentStyle: {backgroundColor: '#fff',}}}>
          <Stack.Screen name="Login" component={Login} />

          <Stack.Screen name="SignUp" component={SignUp} />

          {/* <Stack.Screen name="ForgotPassword1" component={backArrow} /> */}

        </Stack.Navigator>
    </NavigationContainer>
  );
}