import {View, Text, Image, TouchableOpacity} from 'react-native';
import { useNavigation } from '@react-navigation/native';


import sharedStyles from '../../Styles/Shared/style';


import backArrowIcon from '../../assets/icons/back_arrow.png';

export default function BackArrow(){
    const navigation = useNavigation();
    return (
        <View>
            <View style={sharedStyles.arrowBackbuttonBackLayer}>
                <TouchableOpacity style={sharedStyles.arrowBackbuttonFrontLayer} onPress={()=>{navigation.goBack()}}>
                    <Image source={backArrowIcon} style={sharedStyles.arrowBtnIcon} />
                    <Text style={sharedStyles.textBtn}>رجوع إلى الخلف</Text>
                </TouchableOpacity>
            </View>
        </View>
    );
}
