import React, {Component} from 'react';
import {View, Text, Image, TouchableOpacity, TextInput} from 'react-native';

import Header from './Components/Header';
import BackArrow from './Components/BackArrow';


import signUpStyles from '../Styles/SignUp/style';
import sharedStyles from '../Styles/Shared/style';
// const signUpStyles  = require('../Styles/Login/style');
// const sharedStyles = require('../Styles/Shared/style');

import emailIcon from '../assets/icons/signup_email.png';
import institIcon from '../assets/icons/icons8-organization-100.png';
import numberIcon from '../assets/icons/signup_number.png';
import pencilIcon from '../assets/icons/login_signup2.png';
import userIcon from '../assets/icons/login_avatar_3.png';
import domainTypeIcon from '../assets/icons/signup_domain_type.png';
import addressIcon from '../assets/icons/signup_address.png';
import keyIcon from '../assets/icons/login_key2.png';
import showPasswordIcon from '../assets/icons/signup_opendEye.png';
import hidePasswordIcon from '../assets/icons/signup_closedEye.png';





export default class SignUp extends Component {
    state = {
        showPassword: false,
    };

    render() {

        const changeShowPasswordState = () => {
            this.setState({ showPassword: !this.state.showPassword });
        }

        return(
            
            <View style={signUpStyles.view}>

                {/* header */}
                <View style={{width: '100%'}}>
                    <Header />

                    <View style={{width: 120,}}>
                        <BackArrow />
                    </View>
                </View>
                



                {/* card */}
                <View style={signUpStyles.cardBackLayer}>
                    <View style={signUpStyles.cardFrontLayer}>

                        {/* title */}
                        <Text style={sharedStyles.titleText}>إنـشـاء حـسـاب جـديـد</Text>
                        {/* body */}
                        <View style={signUpStyles.body}>


                            {/* body left column */}
                            <View style={signUpStyles.bodyColumn}>

                                {/* domain type */}
                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="مـجـال عـمـل المؤسـسـة" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={domainTypeIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>


                                {/* username text input */}
                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="البريد الإلكتروني" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={emailIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>

                                {/* password text input */}
                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>

                                        {
                                            this.state.showPassword ?
                                                <TouchableOpacity onPress={()=>{changeShowPasswordState()}}>
                                                    <Image source={showPasswordIcon} style={sharedStyles.iconRight} />
                                                </TouchableOpacity>
                                            :
                                                <TouchableOpacity onPress={()=>{changeShowPasswordState()}}>
                                                    <Image source={hidePasswordIcon} style={sharedStyles.iconRight} />
                                                </TouchableOpacity>
                                        }
                                        
                                        <TextInput placeholder="كــلمة المرور" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} secureTextEntry={!this.state.showPassword} autoComplete={'off'} />
                                        <Image source={keyIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>
                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="كرر كــلمة المرور " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} secureTextEntry={!this.state.showPassword} autoComplete={'off'} />
                                        <Image source={keyIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>



                                {/* login btn */}
                                <View style={{width: '65%', marginTop: 12, alignSelf: 'center'}}>
                                    <View style={sharedStyles.buttonBackLayer}>
                                        <TouchableOpacity style={sharedStyles.buttonFrontLayer}>
                                            <Text style={sharedStyles.textBtn}>تـسـجـيـل</Text>
                                            <Image source={pencilIcon} style={sharedStyles.btnIcon} />
                                        </TouchableOpacity>
                                    </View>
                                </View>

                            </View>
                            {/*  end of body left column */}


                            
                            {/* body right column */}
                            <View style={signUpStyles.bodyColumn}>

                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="إسـم المؤسـسـة" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={institIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>

                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="إسـم المـستـخدم (يجب أن يكـون مـمـيز)" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={userIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>

                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <View style={signUpStyles.phoneContainer}>
                                            <Text style={{fontSize: 18, color: '#707070'}}>+249    | </Text>
                                            <TextInput placeholder="الرقم (بدون صفر)" placeholderTextColor={'#C7C7CD'} style={signUpStyles.textInputPhone} 
                                                autoCapitalize={'none'} autoComplete={'off'} numeric={true}  maxLength={9} keyboardType="numeric"/>
                                        </View>
                                        <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                    </View>
                                </View>

                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="الــرقـم التـعـريـفـي الـضـريـبـي" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                    </View>
                                </View>

                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <TextInput placeholder="الــعنــوان" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                            autoCapitalize={'none'} autoComplete={'off'} />
                                        <Image source={addressIcon} style={sharedStyles.icon} />
                                    </View>
                                </View>
                            </View>
                            {/* end of body right column */}

                        </View>
                    </View>
                </View>


            </View>

        );


    }


}